/**
 * segmentit 라이브러리를 사용한 중국어 토크나이저
 * https://github.com/linonetwo/segmentit
 */
import pinyin from "pinyin";
import { Segment, useDefault } from "segmentit";

export class SegmentitTokenizer {
  constructor() {
    this.chineseCharRegex = /[\u4e00-\u9fff]/g;
    this.initialized = false;
    this.segmentit = null;
  }

  /**
   * segmentit 토크나이저 초기화
   */
  async initialize() {
    try {
      console.log("segmentit 토크나이저 초기화 중...");
      this.segmentit = useDefault(new Segment());
      this.initialized = true;
      console.log("segmentit 토크나이저 초기화 완료");
      return true;
    } catch (error) {
      console.error("segmentit 토크나이저 초기화 실패:", error);
      // 폴백: 기본 토크나이저 사용
      this.initialized = true;
      return false;
    }
  }

  /**
   * segmentit을 사용한 중국어 토큰화
   * @param {string} text - 중국어 텍스트
   * @returns {Promise<string[]>} 토큰 배열
   */
  async tokenize(text) {
    if (!text) return [];

    if (this.segmentit && this.initialized) {
      try {
        // segmentit 사용
        const result = this.segmentit.doSegment(text);
        console.log("segmentit 원본 결과:", result);
        console.log("result 구조:", JSON.stringify(result, null, 2));
        return result
          .map((item) => item.w)
          .filter((word) => word.trim().length > 0)
          .filter((word) => this.isValidWord(word)); // 특수문자 제거
      } catch (error) {
        console.error("segmentit 토큰화 실패, 폴백 사용:", error);
        return this.fallbackTokenize(text);
      }
    } else {
      return this.fallbackTokenize(text);
    }
  }

  /**
   * 단어가 유효한지 확인 (특수문자 제거)
   * @param {string} word - 검사할 단어
   * @returns {boolean} 유효한 단어 여부
   */
  isValidWord(word) {
    if (!word || word.trim().length === 0) return false;

    const trimmed = word.trim();

    // 중국어 문자만 포함하는지 확인
    if (!/^[\u4e00-\u9fff]+$/.test(trimmed)) return false;

    // 특수문자가 포함되지 않았는지 확인
    if (/[^\u4e00-\u9fff]/.test(trimmed)) return false;

    // 중국어 문장부호가 포함되지 않았는지 확인
    if (/[，。！？；：""''（）【】《》、…—]/.test(trimmed)) return false;

    return true;
  }

  /**
   * 폴백 토큰화 (기존 알고리즘)
   * @param {string} text - 중국어 텍스트
   * @returns {string[]} 토큰 배열
   */
  fallbackTokenize(text) {
    if (!text) return [];

    const normalized = this.normalize(text);
    const chineseChars = normalized.match(this.chineseCharRegex) || [];

    // 사전 기반 토큰화
    const tokens = [];
    let i = 0;

    while (i < chineseChars.length) {
      let found = false;

      // 4글자부터 1글자까지 역순으로 검색
      for (let len = Math.min(4, chineseChars.length - i); len >= 1; len--) {
        const candidate = chineseChars.slice(i, i + len).join("");

        // 사전에 있는 단어인지 확인
        if (this.commonWords.has(candidate)) {
          tokens.push(candidate);
          i += len;
          found = true;
          break;
        }
      }

      // 사전에 없는 경우 2글자씩 묶어서 처리
      if (!found) {
        const word = chineseChars.slice(i, i + 2).join("");
        tokens.push(word);
        i += 2;
      }
    }

    return tokens.filter((word) => word.trim().length > 0);
  }

  /**
   * 중국어 텍스트를 문자 단위로 토큰화
   * @param {string} text - 중국어 텍스트
   * @returns {string[]} 문자 단위 토큰 배열
   */
  tokenizeByChar(text) {
    if (!text) return [];
    const chineseChars = text.match(this.chineseCharRegex) || [];
    return chineseChars;
  }

  /**
   * 중국어 텍스트를 단어 단위로 토큰화
   * @param {string} text - 중국어 텍스트
   * @returns {Promise<string[]>} 단어 단위 토큰 배열
   */
  async tokenizeByWord(text) {
    return await this.tokenize(text);
  }

  /**
   * 두 중국어 텍스트의 유사도 계산
   * @param {string} text1 - 첫 번째 텍스트
   * @param {string} text2 - 두 번째 텍스트
   * @returns {Promise<number>} 유사도 점수 (0~1)
   */
  async calculateSimilarity(text1, text2) {
    const tokens1 = await this.tokenize(text1);
    const tokens2 = await this.tokenize(text2);

    if (tokens1.length === 0 && tokens2.length === 0) return 1.0;
    if (tokens1.length === 0 || tokens2.length === 0) return 0.0;

    const set1 = new Set(tokens1);
    const set2 = new Set(tokens2);

    const intersection = new Set([...set1].filter((x) => set2.has(x)));
    const union = new Set([...set1, ...set2]);

    return intersection.size / union.size;
  }

  /**
   * 병음 변환
   * @param {string} text - 중국어 텍스트
   * @returns {Promise<string>} 병음 문자열
   */
  async convertToPinyin(text) {
    if (!text) return "";

    try {
      const pinyinResult = pinyin(text, {
        style: pinyin.STYLE_TONE,
        heteronym: false,
      });
      return pinyinResult.flat().join(" ");
    } catch (error) {
      console.error("병음 변환 실패:", error);
      return this.fallbackPinyin(text);
    }
  }

  /**
   * 폴백 병음 변환
   * @param {string} text - 중국어 텍스트
   * @returns {string} 병음 문자열
   */
  fallbackPinyin(text) {
    const pinyinMap = {
      我: "wǒ",
      你: "nǐ",
      他: "tā",
      她: "tā",
      它: "tā",
      我们: "wǒmen",
      你们: "nǐmen",
      他们: "tāmen",
      好: "hǎo",
      坏: "huài",
      大: "dà",
      小: "xiǎo",
      学习: "xuéxí",
      工作: "gōngzuò",
      吃饭: "chīfàn",
      今天: "jīntiān",
      明天: "míngtiān",
      昨天: "zuótiān",
      中国: "zhōngguó",
      美国: "měiguó",
      日本: "rìběn",
      北京: "běijīng",
      上海: "shànghǎi",
      广州: "guǎngzhōu",
      世界: "shìjiè",
      学生: "xuéshēng",
      喜欢: "xǐhuān",
      中文: "zhōngwén",
    };

    return text
      .split("")
      .map((char) => pinyinMap[char] || char)
      .join(" ");
  }

  /**
   * 텍스트 정규화
   * @param {string} text - 정규화할 텍스트
   * @returns {string} 정규화된 텍스트
   */
  normalize(text) {
    if (!text) return "";

    return text
      .replace(/[\u3000\u2000-\u200f\u2028-\u202f\u205f-\u206f]/g, " ") // 전각 공백을 반각으로
      .replace(/\s+/g, " ") // 연속된 공백을 하나로
      .trim();
  }

  /**
   * 차이점 분석
   * @param {string} originalText - 원문
   * @param {string} compareText - 비교할 문장
   * @returns {Promise<object>} 차이점 분석 결과
   */
  async analyzeDifferences(originalText, compareText) {
    const originalTokens = await this.tokenize(originalText);
    const compareTokens = await this.tokenize(compareText);

    const originalSet = new Set(originalTokens);
    const compareSet = new Set(compareTokens);

    const omissions = originalTokens.filter((token) => !compareSet.has(token));
    const additions = compareTokens.filter((token) => !originalSet.has(token));

    return {
      omissions,
      additions,
      similarity: await this.calculateSimilarity(originalText, compareText),
    };
  }

  /**
   * 초기화 상태 확인
   * @returns {boolean} 초기화 여부
   */
  isInitialized() {
    return this.initialized;
  }
}
