<template>
  <div>
    <h1>DKVoiceWeb Vue3 예제</h1>

    <!-- 환경 설정 -->
    <div class="environment-settings">
      <label>
        환경 선택:
        <select v-model="currentEnvironment" @change="changeEnvironment">
          <option value="dev">DEV</option>
          <option value="stg">STG</option>
          <option value="prod">PROD</option>
        </select>
      </label>
      <span class="current-token">현재 토큰: {{ getCurrentToken() }}</span>
    </div>

    <!-- 탭 네비게이션 -->
    <div class="tab-navigation">
      <button
        @click="activeTab = 'voice-recorder'"
        :class="{ active: activeTab === 'voice-recorder' }"
        class="tab-btn"
      >
        음성 녹음
      </button>
      <button
        @click="activeTab = 'chinese-compare'"
        :class="{ active: activeTab === 'chinese-compare' }"
        class="tab-btn"
      >
        중국어 비교
      </button>

      <button
        @click="activeTab = 'jieba-test'"
        :class="{ active: activeTab === 'jieba-test' }"
        class="tab-btn"
      >
        토크나이저 테스트
      </button>

      <button
        @click="activeTab = 'voice-upload'"
        :class="{ active: activeTab === 'voice-upload' }"
        class="tab-btn"
      >
        음성 파일 업로드
      </button>
    </div>

    <!-- 음성 녹음 탭 -->
    <div v-if="activeTab === 'voice-recorder'" class="tab-panel">
      <div style="margin-bottom: 1em">
        <label>
          무음 감지 임계값 (silenceThreshold):
          <input
            type="number"
            step="0.01"
            min="0"
            max="1"
            v-model.number="silenceThreshold"
          />
        </label>
        <label style="margin-left: 1em">
          무음 지속 시간(ms) (silenceDuration):
          <input
            type="number"
            step="100"
            min="500"
            v-model.number="silenceDuration"
          />
        </label>
      </div>

      <!-- 원문 텍스트 입력 -->
      <div style="margin-bottom: 1em">
        <label for="originalText" style="display: block; margin-bottom: 0.5em">
          <strong>원문 텍스트:</strong>
        </label>
        <textarea
          id="originalText"
          v-model="originalText"
          placeholder="원문 텍스트를 입력하세요. 예: 你好世界"
          rows="3"
          cols="60"
          style="width: 100%; max-width: 500px"
        ></textarea>
      </div>

      <button @click="startRecording" :disabled="isRecording">녹음 시작</button>
      <button @click="stopRecording" :disabled="!isRecording">녹음 종료</button>
      <div v-if="audioUrl">
        <h2>녹음 파일 미리듣기</h2>
        <audio :src="audioUrl" controls />
      </div>
      <div v-if="loading">처리 중...</div>
      <div v-if="result && result.sttText">
        <h2>인식 결과</h2>
        <p><strong>STT 텍스트:</strong> {{ result.sttText }}</p>
        <p><strong>유사도:</strong> {{ result.score || "N/A" }}점</p>
        <p>
          <strong>신뢰도:</strong>
          {{
            result.confidence ? (result.confidence * 100).toFixed(1) : "N/A"
          }}%
        </p>
        <p>
          <strong>발음 점수:</strong>
          {{
            result.pronunciationScore
              ? result.pronunciationScore.toFixed(1)
              : "N/A"
          }}점
        </p>
        <h2>피드백: {{ result.message || "분석 완료" }}</h2>

        <!-- 3-Way 분석 결과 -->
        <div
          v-if="result.analysis && result.analysis.data"
          style="margin-top: 20px"
        >
          <h3>3-Way 분석 결과</h3>
          <div style="margin-left: 20px; font-size: 0.9em">
            <p>
              <strong>텍스트 유사도:</strong>
              {{
                (result.analysis.data.textSimilarity?.similarity * 100).toFixed(
                  1
                )
              }}%
            </p>
            <p>
              <strong>정확도:</strong>
              {{
                (result.analysis.data.textSimilarity?.accuracy * 100).toFixed(
                  1
                )
              }}%
            </p>

            <!-- 단어별 분석 결과 -->
            <div v-if="result.analysis.data.wordAnalysis">
              <h4>단어별 분석:</h4>
              <div
                v-if="
                  result.analysis.data.wordAnalysis.wrongWords &&
                  result.analysis.data.wordAnalysis.wrongWords.length > 0
                "
              >
                <p>
                  <strong>틀린 단어:</strong>
                  {{ result.analysis.data.wordAnalysis.wrongWords.join(", ") }}
                </p>
              </div>
              <div
                v-if="
                  result.analysis.data.wordAnalysis.insertions &&
                  result.analysis.data.wordAnalysis.insertions.length > 0
                "
              >
                <p>
                  <strong>삽입된 단어:</strong>
                  {{ result.analysis.data.wordAnalysis.insertions.join(", ") }}
                </p>
              </div>
              <div
                v-if="
                  result.analysis.data.wordAnalysis.omissions &&
                  result.analysis.data.wordAnalysis.omissions.length > 0
                "
              >
                <p>
                  <strong>생략된 단어:</strong>
                  {{ result.analysis.data.wordAnalysis.omissions.join(", ") }}
                </p>
              </div>
            </div>

            <!-- 발음 평가 결과 -->
            <div
              v-if="
                result.analysis.data.srAnalysis &&
                result.analysis.data.srAnalysis.pronunciationAssessment
              "
            >
              <h4>발음 평가:</h4>
              <p>
                <strong>전체 발음 점수:</strong>
                {{
                  result.analysis.data.srAnalysis.pronunciationAssessment.overallScore?.toFixed(
                    1
                  ) || "N/A"
                }}점
              </p>
              <p>
                <strong>정확도 점수:</strong>
                {{
                  result.analysis.data.srAnalysis.pronunciationAssessment.accuracyScore?.toFixed(
                    1
                  ) || "N/A"
                }}점
              </p>
              <p>
                <strong>유창성 점수:</strong>
                {{
                  result.analysis.data.srAnalysis.pronunciationAssessment.fluencyScore?.toFixed(
                    1
                  ) || "N/A"
                }}점
              </p>
              <p>
                <strong>완성도 점수:</strong>
                {{
                  result.analysis.data.srAnalysis.pronunciationAssessment.completenessScore?.toFixed(
                    1
                  ) || "N/A"
                }}점
              </p>
            </div>

            <!-- 단어별 발음 분석 -->
            <div
              v-if="
                result.analysis.data.srAnalysis &&
                result.analysis.data.srAnalysis.wordPronunciation
              "
            >
              <h4>단어별 발음 분석:</h4>
              <p>
                <strong>총 단어 수:</strong>
                {{
                  result.analysis.data.srAnalysis.wordPronunciation.totalWords
                }}개
              </p>
              <p>
                <strong>정확한 단어:</strong>
                {{
                  result.analysis.data.srAnalysis.wordPronunciation
                    .accurateWords
                }}개
              </p>
              <p>
                <strong>발음 오류 단어:</strong>
                {{
                  result.analysis.data.srAnalysis.wordPronunciation
                    .mispronouncedWords
                }}개
              </p>
              <p>
                <strong>생략된 단어:</strong>
                {{
                  result.analysis.data.srAnalysis.wordPronunciation
                    .omittedWords
                }}개
              </p>
              <p>
                <strong>삽입된 단어:</strong>
                {{
                  result.analysis.data.srAnalysis.wordPronunciation
                    .insertedWords
                }}개
              </p>
              <p>
                <strong>평균 정확도:</strong>
                {{
                  result.analysis.data.srAnalysis.wordPronunciation.averageAccuracy?.toFixed(
                    1
                  ) || "N/A"
                }}점
              </p>

              <!-- 발음 오류 상세 정보 -->
              <div
                v-if="
                  result.analysis.data.srAnalysis.wordPronunciation
                    .wordDetails &&
                  result.analysis.data.srAnalysis.wordPronunciation.wordDetails
                    .length > 0
                "
              >
                <h5>발음 오류 상세:</h5>
                <div
                  v-for="(word, index) in lowScoreWords"
                  :key="index"
                  style="
                    margin-left: 20px;
                    margin-bottom: 10px;
                    padding: 10px;
                    border: 1px solid #ddd;
                    border-radius: 5px;
                  "
                >
                  <p><strong>단어:</strong> {{ word.word }}</p>
                  <p>
                    <strong>발음 점수:</strong>
                    {{ word.accuracyScore?.toFixed(1) || "N/A" }}점
                  </p>
                  <p><strong>오류 타입:</strong> {{ word.errorType }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- SR API 상세 정보 -->
        <details v-if="result.srResult">
          <summary>SR API 상세 정보</summary>
          <div style="margin-left: 20px; font-size: 0.9em">
            <p>
              <strong>인식 상태:</strong>
              {{ result.srResult.RecognitionStatus }}
            </p>
            <p>
              <strong>SNR:</strong>
              {{ result.srResult.SNR?.toFixed(2) || "N/A" }}
            </p>
            <p>
              <strong>지속 시간:</strong>
              {{
                result.srResult.Duration
                  ? (result.srResult.Duration / 10000000).toFixed(2)
                  : "N/A"
              }}초
            </p>

            <div v-if="result.srResult.NBest && result.srResult.NBest[0]">
              <h4>N-Best 결과:</h4>
              <p>
                <strong>Lexical:</strong> {{ result.srResult.NBest[0].Lexical }}
              </p>
              <p><strong>ITN:</strong> {{ result.srResult.NBest[0].ITN }}</p>

              <div v-if="result.srResult.NBest[0].PronunciationAssessment">
                <h4>발음 평가:</h4>
                <p>
                  <strong>정확도:</strong>
                  {{
                    result.srResult.NBest[0].PronunciationAssessment
                      .AccuracyScore
                  }}점
                </p>
                <p>
                  <strong>유창성:</strong>
                  {{
                    result.srResult.NBest[0].PronunciationAssessment
                      .FluencyScore
                  }}점
                </p>
                <p>
                  <strong>완성도:</strong>
                  {{
                    result.srResult.NBest[0].PronunciationAssessment
                      .CompletenessScore
                  }}점
                </p>
              </div>
            </div>
          </div>
        </details>
      </div>
    </div>

    <!-- 중국어 비교 탭 -->
    <div v-if="activeTab === 'chinese-compare'" class="tab-panel">
      <ChineseCompare
        :uploadResult="uploadResult"
        :referenceText="uploadReferenceText"
      />
    </div>

    <!-- jieba 테스트 탭 -->
    <div v-if="activeTab === 'jieba-test'" class="tab-panel">
      <h2>중국어 토크나이저 테스트</h2>
      <p>segmentit 라이브러리를 사용한 중국어 토크나이저를 테스트합니다.</p>

      <div class="input-section">
        <label for="testText">테스트할 중국어 텍스트:</label>
        <textarea
          id="testText"
          v-model="testText"
          placeholder="중국어 텍스트를 입력하세요. 예: 尽管吃了不少苦，但是那段时光最终成为了难以忘记的美好回忆。"
          rows="4"
          cols="60"
        ></textarea>

        <div class="button-group">
          <button @click="testJieba" :disabled="!testText.trim()">
            토크나이저 테스트 실행
          </button>
          <button @click="clearTest" class="clear-btn">초기화</button>
        </div>
      </div>

      <div v-if="tokenizerTestResult" class="result-section">
        <h3>테스트 결과:</h3>
        <div class="result-content">
          <div class="result-item">
            <strong>입력 텍스트:</strong>
            <p>{{ testText }}</p>
          </div>
          <div class="result-item">
            <strong>토큰화 결과:</strong>
            <pre>{{ tokenizerTestResult.tokens }}</pre>
          </div>
          <div class="result-item">
            <strong>병음 변환:</strong>
            <pre>{{ tokenizerTestResult.pinyin }}</pre>
          </div>
          <div class="result-item">
            <strong>토큰 개수:</strong>
            <span>{{ tokenizerTestResult.tokenCount }}개</span>
          </div>
        </div>
      </div>
    </div>

    <!-- 음성 파일 업로드 탭 -->
    <div v-if="activeTab === 'voice-upload'" class="tab-panel">
      <h2>음성 파일 업로드</h2>

      <!-- 인증 토큰 섹션 -->
      <div class="auth-section">
        <h3>인증 토큰</h3>
        <div class="token-input-group">
          <label for="authToken">Authorization Token:</label>
          <div class="token-input-container">
            <input
              id="authToken"
              v-model="authToken"
              type="text"
              placeholder="토큰을 입력하거나 '토큰 받기' 버튼을 클릭하세요"
              class="token-input"
              @input="handleTokenInput"
            />
            <button
              @click="copyToken"
              :disabled="!authToken"
              class="copy-token-btn"
              :title="authToken ? '토큰 복사' : '복사할 토큰이 없습니다'"
            >
              📋
            </button>
          </div>
          <button
            @click="getAuthToken"
            :disabled="isGettingToken"
            class="get-token-btn"
          >
            {{ isGettingToken ? "토큰 요청 중..." : "토큰 받기" }}
          </button>
        </div>
        <div v-if="tokenStatus" class="token-status" :class="tokenStatus.type">
          {{ tokenStatus.message }}
        </div>
      </div>

      <!-- 파일 업로드 섹션 -->
      <div class="upload-section">
        <h3>음성 파일 업로드</h3>

        <!-- 파일 선택 또는 녹음 -->
        <div class="file-input-group">
          <h4>방법 1: 기존 파일 선택</h4>
          <label for="audioFile">음성 파일 선택:</label>
          <input
            id="audioFile"
            type="file"
            @change="handleFileSelect"
            accept="audio/*"
            class="file-input"
          />

          <h4 style="margin-top: 20px">방법 2: 음성 녹음</h4>
          <div class="auto-upload-notice">
            <p>🎤 녹음이 완료되면 자동으로 파일 업로드가 시작됩니다</p>
          </div>
          <div class="recording-controls">
            <div class="recording-settings">
              <label>
                무음 감지 임계값:
                <input
                  type="number"
                  step="0.01"
                  min="0"
                  max="1"
                  v-model.number="uploadSilenceThreshold"
                  class="setting-input-small"
                />
              </label>
              <label style="margin-left: 1em">
                무음 지속 시간(ms):
                <input
                  type="number"
                  step="100"
                  min="500"
                  v-model.number="uploadSilenceDuration"
                  class="setting-input-small"
                />
              </label>
            </div>
            <div class="recording-buttons">
              <button
                @click="startUploadRecording"
                :disabled="isUploadRecording"
                class="record-btn"
              >
                녹음 시작
              </button>
              <button
                @click="stopUploadRecording"
                :disabled="!isUploadRecording"
                class="stop-btn"
              >
                녹음 종료
              </button>
            </div>
            <div v-if="uploadAudioUrl" class="recording-preview">
              <h5>녹음 파일 미리듣기:</h5>
              <audio :src="uploadAudioUrl" controls />
            </div>
          </div>

          <div v-if="selectedFile" class="file-info">
            <p><strong>선택된 파일:</strong> {{ selectedFile.name }}</p>
            <p>
              <strong>파일 크기:</strong>
              {{ formatFileSize(selectedFile.size) }}
            </p>
            <p><strong>파일 타입:</strong> {{ selectedFile.type }}</p>
            <p v-if="audioDuration">
              <strong>음성 길이:</strong> {{ formatDuration(audioDuration) }}
            </p>
          </div>
        </div>

        <!-- 업로드 설정 -->
        <div class="upload-settings">
          <div class="setting-group">
            <label for="subjectCode">과목 코드:</label>
            <select
              id="subjectCode"
              v-model="uploadSubjectCode"
              class="setting-input"
            >
              <option value="CTT">차이홍 (CTT)</option>
              <option value="PDK">스코어국어 (PDK)</option>
              <option value="STK">스텝국어 (STK)</option>
              <option value="STE">스텝영어 (STE)</option>
            </select>
          </div>

          <div class="setting-group">
            <label for="referenceText">정답 텍스트:</label>
            <textarea
              id="referenceText"
              v-model="uploadReferenceText"
              placeholder="음성 파일과 비교할 정답 텍스트를 입력하세요"
              rows="3"
              class="setting-input"
              style="resize: vertical; min-height: 60px"
            ></textarea>
          </div>
        </div>

        <!-- Presigned URL 표시 -->
        <div
          v-if="presignedUrl"
          class="upload-section"
          style="margin-top: 10px"
        >
          <h3>Presigned 업로드 URL</h3>
          <div class="token-input-container">
            <input
              class="token-input"
              :value="presignedUrl"
              readonly
              placeholder="Presigned URL"
            />
            <button
              @click="copyPresignedUrl"
              class="copy-token-btn"
              :title="'URL 복사'"
            >
              📋
            </button>
          </div>
        </div>

        <!-- 업로드 버튼 -->
        <div class="upload-actions">
          <button
            @click="uploadAudioFile"
            :disabled="!canUpload"
            class="upload-btn"
          >
            {{ isUploading ? uploadStep || "업로드 중..." : "파일 업로드" }}
          </button>
          <button @click="clearUpload" class="clear-upload-btn">초기화</button>
        </div>

        <!-- 업로드 진행 상태 표시 -->
        <div v-if="isUploading && uploadStep" class="upload-progress">
          <div class="progress-info">
            <span class="progress-text">{{ uploadStep }}</span>
            <div class="progress-spinner"></div>
          </div>
        </div>

        <!-- 업로드 결과 -->
        <div v-if="uploadResult" class="upload-result">
          <h4>업로드 결과:</h4>
          <div class="result-details">
            <p>
              <strong>상태:</strong>
              {{ uploadResult.success ? "성공" : "실패" }}
            </p>
            <p v-if="uploadResult.fileId">
              <strong>파일 ID:</strong> {{ uploadResult.fileId }}
            </p>
            <p v-if="uploadResult.url">
              <strong>업로드 URL:</strong> {{ uploadResult.url }}
            </p>
            <p v-if="uploadResult.message">
              <strong>메시지:</strong> {{ uploadResult.message }}
            </p>

            <!-- Azure 평가 결과 표시 -->
            <div v-if="uploadResult.evaluateResult" class="evaluate-result">
              <h5>Azure 평가 결과:</h5>

              <!-- 음성 파일 미리듣기 -->
              <div v-if="getSpeechFileUrl(uploadResult)" class="speech-preview">
                <h6>🎵 인식된 음성 파일 미리듣기:</h6>
                <div class="audio-preview-container">
                  <div class="audio-info">
                    <p>
                      <strong>파일 URL:</strong>
                      {{ getSpeechFileUrl(uploadResult) }}
                    </p>
                    <p v-if="uploadResult.evaluateResult.data?.speechText">
                      <strong>인식된 텍스트:</strong> "{{
                        uploadResult.evaluateResult.data.speechText
                      }}"
                    </p>
                  </div>
                  <div class="audio-controls">
                    <button
                      @click="
                        playAuthenticatedAudio(getSpeechFileUrl(uploadResult))
                      "
                      :disabled="isLoadingAudio"
                      class="play-audio-btn"
                    >
                      {{ isLoadingAudio ? "로딩 중..." : "🎵 재생" }}
                    </button>
                    <button
                      v-if="authenticatedAudioUrl"
                      @click="stopAuthenticatedAudio"
                      class="stop-audio-btn"
                    >
                      ⏹️ 중지
                    </button>
                  </div>

                  <!-- 인증된 오디오 플레이어 -->
                  <div
                    v-if="authenticatedAudioUrl"
                    class="authenticated-audio-player"
                  >
                    <audio
                      :src="authenticatedAudioUrl"
                      controls
                      @ended="handleAudioEnded"
                      @error="handleAudioError"
                      class="speech-audio-player"
                    />
                  </div>

                  <!-- 오디오 로딩 상태 -->
                  <div v-if="isLoadingAudio" class="audio-loading">
                    <div class="loading-spinner"></div>
                    <span>토큰 인증으로 오디오 파일 로딩 중...</span>
                  </div>

                  <!-- 오디오 에러 -->
                  <div v-if="audioError" class="audio-error">
                    <p>
                      <strong>❌ 오디오 로드 실패:</strong> {{ audioError }}
                    </p>
                  </div>
                </div>
              </div>

              <div class="evaluate-details">
                <p v-if="uploadResult.evaluateResult.statusCode">
                  <strong>상태 코드:</strong>
                  {{ uploadResult.evaluateResult.statusCode }}
                </p>
                <p v-if="uploadResult.evaluateResult.message">
                  <strong>메시지:</strong>
                  {{ uploadResult.evaluateResult.message }}
                </p>
                <div v-if="uploadResult.evaluateResult.data">
                  <strong>평가 데이터:</strong>
                  <pre>{{ JSON.stringify(uploadResult, null, 2) }}</pre>
                </div>

