// 인증 토큰 받기
async function getAuthToken() {
  isGettingToken.value = true;
  tokenStatus.value = null;

  try {
    // 현재 환경에 따라 API 경로 결정
    const currentEnv = getEnvironmentFromEnv();
    let apiPath;
    if (currentEnv === "prod") {
      apiPath = "/api/dev/token";
    } else if (currentEnv === "stg") {
      apiPath = "/api/dev/token";
    } else {
      apiPath = "/api/dev/token";
    }

    const response = await fetch(apiPath, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        studentId: "1234567890",
        studentTypeCode: "DI", // DI: 디지털 제품, MA: 마카다미아 이용 제품 (차이홍 등등)
      }),
    });

    if (response.ok) {
      // 응답이 JSON인 경우
      const responseData = await response.json();
      console.log("토큰 응답 (JSON):", responseData);

      // data 필드에서 토큰 추출
      if (responseData.data) {
        authToken.value = responseData.data; // "Bearer " 제거, 토큰만 저장

        // 🔑 받은 토큰을 라이브러리에 설정
        initToken(responseData.data);

        tokenStatus.value = {
          type: "success",
          message: `토큰을 성공적으로 받았습니다. 라이브러리에 자동 설정됨 (${responseData.message})`,
        };
        console.log("인증 토큰 받기 성공:", responseData.data);
        console.log("🔑 라이브러리 토큰 자동 설정 완료");
      } else {
        tokenStatus.value = {
          type: "error",
          message: "토큰 데이터가 응답에 없습니다.",
        };
        console.error("토큰 데이터 없음:", responseData);
      }
    } else {
      // 에러 응답도 JSON으로 처리
      const errorData = await response.json();
      tokenStatus.value = {
        type: "error",
        message: `토큰 요청 실패: ${errorData.message || "알 수 없는 오류"}`,
