// pronunciationResults를 HTML로 변환하는 함수
function generatePronunciationHtml(pronunciationResults) {
  console.log("generatePronunciationHtml !!!!!!!!!!!!!!!!!!!!!!  시작111");
  console.log("generatePronunciationHtml 값 :", pronunciationResults);
  if (!pronunciationResults || !Array.isArray(pronunciationResults)) {
    return "";
  }

  console.log("generatePronunciationHtml !!!!!!!!!!!!!!!!!!!!!!  시작222");
  let html = '<div class="pronunciation-results">';

  pronunciationResults.forEach((item, index) => {
    const { type, value, operation, hasMispronunciation, mispronunciations } =
      item;
    console.log("operation:", operation);
    console.log("type:", type, "hasMispronunciation:", hasMispronunciation);

    // diffInfo.type에 따른 색상 클래스 결정
    let diffColorClass = "";
    switch (type) {
      case "common":
        // 공통은 색상 표시하지 않음
        diffColorClass = "";
        break;
      case "moved":
        diffColorClass = "diff-moved";
        break;
      case "added": //순수 추가
        diffColorClass = "diff-added";
        break;
      case "removed": //순수 삭제
        diffColorClass = "diff-removed";
        break;
      case "moved_from":
        diffColorClass = "diff-moved-from";
        break;
      default:
        diffColorClass = "";
    }

    // 기본 표시 텍스트
    let processedValue = value;

    // 규칙: 직전 항목이 removed이고 현재가 added이면, 추가 텍스트를 발음오류로 간주하여 강조
    if (type === "added") {
      const prev = index > 0 ? pronunciationResults[index - 1] : null;
      if (prev && prev.type === "removed") {
        console.log("교체 패턴 감지: removed 다음의 added를 발음 오류로 처리", {
          removed: prev.value,
          added: value,
        });
        processedValue = `<span class="mispronunciation-error" title="발음 오류: 교체로 추가된 텍스트">${value}</span>`;
      }
    }

    // type이 "common"이고 hasMispronunciation이 true인 경우 발음 오류 처리
    if (
      type === "common" &&
      hasMispronunciation &&
      mispronunciations &&
      mispronunciations.length > 0
    ) {
      console.log("발음 오류 처리:", mispronunciations);

      // value 텍스트에서 mispronunciations의 word의 "N번째 발생"을 찾아 색칠
      // (common 케이스는 위의 processedValue를 덮어쓴다)
      const mispronunciationClass = "mispronunciation-error";

      // 치환 범위 충돌 방지를 위해, 먼저 감쌀 구간(start,end)을 모두 수집 후 뒤에서 앞으로 치환
      const ranges = [];

      mispronunciations.forEach((mis) => {
        const { word, wordIndex = 0 } = mis;
        if (!word || typeof word !== "string" || word.length === 0) return;

        // 현재 value에서 해당 word의 모든 발생 위치 수집
        const positions = [];
        const regex = new RegExp(
          word.replace(/[.*+?^${}()|[\]\\]/g, "\\$&"),
          "g"
        );
        let match;
        while ((match = regex.exec(value)) !== null) {
          positions.push({
            start: match.index,
            end: match.index + word.length,
          });
          // 무한 루프 방지: 0길이 매치일 경우 한 글자 전진
          if (match.index === regex.lastIndex) regex.lastIndex++;
        }

        if (positions.length === 0) {
          console.warn(`값 내에서 단어를 찾지 못함: word="${word}"`);
          return;
        }

        // wordIndex가 세그먼트 전체가 아닌 문장 기준일 수 있으므로, 세그먼트 내 발생 개수 범위로 보정
        const idx = Math.min(Math.max(0, wordIndex), positions.length - 1);
        const target = positions[idx];
        if (target) {
          ranges.push({ ...target, word });
        }
      });

      // 겹치는 범위를 제거(간단히 시작 인덱스 기준 정렬 후 병합 불가한 것만 남김)
      ranges.sort((a, b) => a.start - b.start);
      const filtered = [];
      let lastEnd = -1;
      for (const r of ranges) {
        if (r.start >= lastEnd) {
          filtered.push(r);
          lastEnd = r.end;
        } else {
          // 겹치면 더 긴 범위를 우선시하도록 교체
          const prev = filtered[filtered.length - 1];
          if (r.end - r.start > prev.end - prev.start) {
            filtered[filtered.length - 1] = r;
            lastEnd = r.end;
          }
        }
      }

      // 뒤에서 앞으로 치환
      for (let i = filtered.length - 1; i >= 0; i--) {
        const { start, end, word } = filtered[i];
        const before = processedValue.slice(0, start);
        const middle = processedValue.slice(start, end);
        const after = processedValue.slice(end);
        processedValue = `${before}<span class="${mispronunciationClass}" title="발음 오류: ${word}">${middle}</span>${after}`;
      }

      // 최종 클래스 조합
      const finalClass = `pronunciation-word ${diffColorClass}`.trim();

      html += `<span class="${finalClass}" title="오류 유형: ${type}, 차이점 유형: ${
        type || "unknown"
      }">${processedValue}</span>`;
    } else {
      // 일반적인 경우 (발음 오류가 없는 경우)
      const finalClass = `pronunciation-word ${diffColorClass}`.trim();

      html += `<span class="${finalClass}" title="오류 유형: ${type}, 차이점 유형: ${
        type || "unknown"
      }">${processedValue}</span>`;
    }

    // 단어 사이에 공백 추가 (마지막 단어 제외)
    if (index < pronunciationResults.length - 1) {
      html += "";
    }
  });

  html += "</div>";

  console.log("generatePronunciationHtml 결과:", html);

  return html;
}

// 업로드 결과에서 pronunciationResults HTML 생성
const pronunciationResultsHtml = computed(() => {
  console.log(
