});

// pronunciationResults가 있는지 확인
const hasPronunciationResults = computed(() => {
  return !!(
    uploadResult.value?.evaluateResult?.textComparison?.structuredDiffs
      ?.length > 0
  );
});
function startUploadRecording() {
  if (uploadRecorder) {
    uploadRecorder = null;
  }

  uploadRecorder = new VoiceRecorder({
    silenceThreshold: uploadSilenceThreshold.value,
    silenceDuration: uploadSilenceDuration.value,
  });

  uploadRecorder.on("start", () => {
    isUploadRecording.value = true;
    uploadAudioUrl.value = null;
    selectedFile.value = null;
    console.log("업로드용 녹음 시작");
  });

  uploadRecorder.on("stop", async (blob) => {
    isUploadRecording.value = false;
    uploadAudioUrl.value = URL.createObjectURL(blob);

    // Blob을 File 객체로 변환
    const fileName = `recorded_${Date.now()}.wav`;
    const file = new File([blob], fileName, { type: "audio/wav" });

    // 파일 정보 설정
    selectedFile.value = file;

    // 파일 정보 가져오기
    try {
      const fileInfo = await voiceUploadManager.getAudioFileInfo(file);
      audioDuration.value = fileInfo.duration;
      console.log("녹음 파일 정보:", fileInfo);
    } catch (error) {
      console.error("녹음 파일 정보 가져오기 실패:", error);
      audioDuration.value = null;
    }

    console.log("업로드용 녹음 완료:", fileName);

    // 자동으로 파일 업로드 시작
    setTimeout(() => {
      if (canUpload.value) {
        console.log("🚀 녹음 완료 - 자동 업로드 시작");
        uploadAudioFile();
      } else {
        console.warn("⚠️ 업로드 조건이 충족되지 않았습니다.", {
          hasFile: !!selectedFile.value,
          hasToken: !!authToken.value,
          hasSubjectCode: !!uploadSubjectCode.value,
          hasReferenceText: !!uploadReferenceText.value.trim(),
        });
      }
    }, 500); // 0.5초 후 자동 업로드
  });

  uploadRecorder.start();
}

// 업로드용 녹음 종료
function stopUploadRecording() {
  if (uploadRecorder) {
    uploadRecorder.stop();
  }
}

// 업로드 초기화
function clearUpload() {
  selectedFile.value = null;
  audioDuration.value = null; // 음성 파일 길이 초기화
  uploadReferenceText.value = ""; // 정답 텍스트 초기화
  uploadStep.value = ""; // 업로드 단계 초기화
  uploadResult.value = null;
  presignedUrl.value = ""; // 초기화 시 Presigned URL도 초기화

  // 녹음 관련 초기화
  if (uploadRecorder) {
    uploadRecorder = null;
  }
  isUploadRecording.value = false;
  uploadAudioUrl.value = null;

  // 인증된 오디오 초기화
  stopAuthenticatedAudio();

  const fileInput = document.getElementById("audioFile");
  if (fileInput) {
    fileInput.value = "";
  }
  console.log("업로드 초기화 완료");
}

// 환경 변경 함수
function changeEnvironment() {
  console.log(`🌍 환경을 '${currentEnvironment.value}'로 변경합니다.`);

  // DKVoiceWeb 환경 업데이트 - 환경별 API URL 자동 설정
  initializeDKVoiceWeb({
    subjectCode: "MATH101",
    environment: currentEnvironment.value,
    enableLogging: currentEnvironment.value === "dev", // 🆕 개발 환경에서만 로깅 활성화
  });

  // 환경 변경 시 기존 토큰 유지 (사용자가 수동으로 토큰 재설정 필요)

  // 환경 변경 시 VoiceUploadManager 재생성 (새로운 API URL 적용)
  voiceUploadManager = new VoiceUploadManager({
    presignApiUrl: getApiUrl("presigned"),
    azureEvaluateApiUrl: getApiUrl("azureEvaluate"),
  });

  console.log(`📡 새로운 API URLs:`, {
    presigned: getApiUrl("presigned"),
    azureEvaluate: getApiUrl("azureEvaluate"),
  });
  console.log(`🔑 현재 토큰 유지:`, authToken.value ? "설정됨" : "없음");
}

// 현재 토큰 표시 함수
function getCurrentToken() {
  if (authToken.value && authToken.value.trim()) {
    return `${authToken.value.substring(0, 20)}...`;
  }
  return "토큰 없음";
}

let recorder = null; // 동적으로 생성/해제
const uploader = new VoiceUploader(getApiUrl("presigned"));
const stt = new STTClient(getApiUrl("sttRequest"), getApiUrl("sttResult"));
const analyzer = new ThreeWayAnalyzer(); // 3-way 분석기 추가

// 새로운 VoiceUploadManager 사용 - 환경별 API URL 적용
let voiceUploadManager = new VoiceUploadManager({
  presignApiUrl: getApiUrl("presigned"),
  azureEvaluateApiUrl: getApiUrl("azureEvaluate"),
});

function setupRecorder() {
  if (recorder) {
    recorder = null;
  }
  recorder = new VoiceRecorder({
    silenceThreshold: silenceThreshold.value,
    silenceDuration: silenceDuration.value,
  });
  recorder.on("start", () => {
    isRecording.value = true;
    result.value = null;
    audioUrl.value = null;
  });
  recorder.on("stop", async (blob) => {
    isRecording.value = false;
    audioUrl.value = URL.createObjectURL(blob);
    loading.value = true;
    try {
      const fileId = await uploader.upload(blob);
      const sttResult = await stt.requestSTT(fileId);

      // SR API 결과 로그 출력 (간소화)
      console.log("STT 결과:", sttResult);

      // SR API 결과에서 텍스트 추출 (DisplayText 또는 NBest[0].Display 사용)
      const sttText =
        sttResult.DisplayText ||
        (sttResult.NBest && sttResult.NBest[0]
          ? sttResult.NBest[0].Display
          : "") ||
        sttResult.text ||
        "";

      // 3-way 분석 수행 (사용자 입력 원문 텍스트 사용)
      const analysis = await analyzer.analyze(
        originalText.value,
        sttText,
        sttResult
      );

      // SR API 데이터를 포함한 결과 생성
      result.value = {
        message: "음성 인식 및 분석 완료",
        sttText,
        srResult: sttResult, // SR API 전체 결과 포함
        confidence: sttResult.NBest?.[0]?.Confidence || 0,
        pronunciationScore:
          sttResult.NBest?.[0]?.PronunciationAssessment?.PronScore || 0,
        analysis: analysis, // 3-way 분석 결과 추가
      };
    } catch (e) {
      result.value = { message: "오류 발생: " + e.message };
    }
    loading.value = false;
  });
  recorder.on("silence", () => {
    isRecording.value = false;
  });
}

function startRecording() {
  setupRecorder();
  recorder.start();
}
function stopRecording() {
  if (recorder) recorder.stop();
}

// Presigned URL 복사 함수
function copyPresignedUrl() {
  if (!presignedUrl.value) return;
  const temp = document.createElement("input");
  temp.value = presignedUrl.value;
  document.body.appendChild(temp);
  temp.select();
  temp.setSelectionRange(0, 99999);
  document.execCommand("copy");
  document.body.removeChild(temp);
  tokenStatus.value = {
    type: "success",
    message: "Presigned URL이 복사되었습니다!",
  };
}

// speechFileUrl 추출 함수
function getSpeechFileUrl(uploadResult) {
  if (!uploadResult?.evaluateResult?.data?.speechFileUrl) {
    return null;
  }
  return uploadResult.evaluateResult.data.speechFileUrl;
}

// 토큰 인증을 통한 오디오 재생
async function playAuthenticatedAudio(audioUrl) {
  if (!audioUrl || !authToken.value) {
    audioError.value = "오디오 URL 또는 인증 토큰이 없습니다.";
    return;
  }

  isLoadingAudio.value = true;
  audioError.value = null;
  authenticatedAudioUrl.value = null;

  try {
    console.log("🎵 인증된 오디오 요청:", audioUrl);
    console.log("🔑 사용할 토큰:", authToken.value.substring(0, 20) + "...");
    console.log("🔑 토큰 전체 길이:", authToken.value.length);

    console.log(
      "🔑 순수 토큰 (Bearer 제외):",
      authToken.value.substring(0, 30) + "..."
    );

    console.log("🔄 원본 URL:", audioUrl);

    // URL 구조 분석
    const urlObj = new URL(audioUrl);
    console.log("📋 URL 분석:");
    console.log("  - 호스트:", urlObj.hostname);
    console.log("  - 경로:", urlObj.pathname);
    console.log("  - 쿼리:", urlObj.search);

    console.log(
      "🔑 순수 토큰 (Bearer 제외):",
      authToken.value.substring(0, 30) + "..."
    );

    // CORS 회피를 위해 프록시 URL 사용
    const proxyUrl = urlObj.pathname + urlObj.search; // 도메인 제거하고 경로만 사용

    console.log("🔄 원본 URL:", audioUrl);
    console.log("🔄 프록시 URL:", proxyUrl);

    // 프록시를 통해 오디오 파일 요청
    const response = await fetch(proxyUrl, {
      method: "GET",
      headers: {
        Authorization: authToken.value, // Bearer 없이 토큰만
        "Content-Type": "audio/wav",
      },
    });

    if (!response.ok) {
      throw new Error(`HTTP ${response.status}: ${response.statusText}`);
    }

    // 응답을 Blob으로 변환
    const audioBlob = await response.blob();
    console.log("✅ 오디오 파일 로드 성공, 크기:", audioBlob.size, "bytes");

    // Blob URL 생성
    const blobUrl = URL.createObjectURL(audioBlob);
    authenticatedAudioUrl.value = blobUrl;

    console.log("🎵 오디오 재생 준비 완료");

    // 잠깐 기다린 후 자동 재생 시도
    setTimeout(() => {
      const audioElement = document.querySelector(".speech-audio-player");
      if (audioElement) {
        audioElement.play().catch((e) => {
          console.warn("자동 재생 실패 (사용자 상호작용 필요):", e);
        });
      }
    }, 100);
  } catch (error) {
    console.error("❌ 인증된 오디오 로드 실패:", error);
    audioError.value = error.message;
  } finally {
    isLoadingAudio.value = false;
  }
}

// 인증된 오디오 중지
function stopAuthenticatedAudio() {
  if (authenticatedAudioUrl.value) {
    URL.revokeObjectURL(authenticatedAudioUrl.value);
    authenticatedAudioUrl.value = null;
  }
  audioError.value = null;
  console.log("⏹️ 오디오 재생 중지");
}

// 오디오 재생 완료 핸들러
function handleAudioEnded() {
  console.log("🎵 오디오 재생 완료");
}

// 오디오 에러 핸들러
function handleAudioError(event) {
  console.error("❌ 오디오 재생 오류:", event);
  audioError.value = "오디오 재생 중 오류가 발생했습니다.";
}
