// 라이브러리 메서드 확인 (디버그용)
console.log("🔍 TextComparator 메서드 확인:");
console.log("- diffsToHtml:", typeof textComparator.diffsToHtml);
console.log(
  "- diffsToHtmlWithMoveDetection:",
  typeof textComparator.diffsToHtmlWithMoveDetection
);
