// HTML 엔티티 인코딩 유틸리티
function encodeHtml(text) {
  if (text === null || text === undefined) return "";
  return String(text)
    .replace(/&/g, "&amp;")
    .replace(/</g, "&lt;")
    .replace(/>/g, "&gt;")
    .replace(/"/g, "&quot;")
    .replace(/'/g, "&#39;");
}

// structuredDiffs를 HTML 문자열로 변환
function structuredDiffsToHtml(structuredDiffs) {
  if (!Array.isArray(structuredDiffs)) return "";

  let html = "";
  structuredDiffs.forEach((item) => {
    const type = item?.type || "common";
    const value = encodeHtml(item?.value || "");

    switch (type) {
      case "added":
        html += `<span class="added">${value}</span>`;
        break;
      case "removed":
        html += `<span class="removed">${value}</span>`;
        break;
      case "moved":
        html += `<span class="moved">${value}</span>`;
        break;
      case "common":
      default:
        html += `<span>${value}</span>`;
        break;
    }
  });

  return html;
}

