        "라이브러리에서 발음 오류 추출 완료:",
        pronunciationErrors.value
      );
      console.log(
        "발음 통계:",
        analysis.value.pronunciationAnalysis.pronunciationStats
      );
    } else {
      pronunciationErrors.value = [];

      // analysis 객체에 빈 발음 오류 데이터 설정
      if (!analysis.value.pronunciationAnalysis) {
        analysis.value.pronunciationAnalysis = {};
      }
      analysis.value.pronunciationAnalysis.pronunciationErrors = [];

      // 기본 발음 통계 및 임계값 설정
      analysis.value.pronunciationAnalysis.threshold =
        pronunciationThreshold.value;
      analysis.value.pronunciationAnalysis.pronunciationStats = {
        mispronouncedWords: 0,
        averageAccuracy: 100,
        overallScore: 100,
      };

      console.log("발음 평가 데이터가 없습니다.");
    }

    // SR API 전체 데이터 로그 출력
    if (analysis.value.srResult) {
      console.log("🎯 SR API 전체 데이터:", analysis.value.srResult);
      console.log("📝 DisplayText:", analysis.value.srResult.DisplayText);
      console.log("🎯 NBest[0]:", analysis.value.srResult.NBest?.[0]);
      console.log(
        "🔊 PronunciationAssessment:",
        analysis.value.srResult.NBest?.[0]?.PronunciationAssessment
      );
      console.log("📊 Words:", analysis.value.srResult.NBest?.[0]?.Words);
      console.log("🆔 ID:", analysis.value.srResult.Id);
      console.log("📡 SNR:", analysis.value.srResult.SNR);
      console.log("⏱️ Duration:", analysis.value.srResult.Duration);
      console.log("📍 Offset:", analysis.value.srResult.Offset);
      console.log("📺 Channel:", analysis.value.srResult.Channel);
    }

    console.log("분석 결과:", analysis.value);
  } catch (error) {
    console.error("분석 중 오류 발생:", error);
    analysis.value = null;
  }
}

// diff-match-patch 비교 실행 함수
async function performDiffComparison() {
  if (!originalText.value || !compareText.value) {
    alert("원문과 비교 문장을 모두 입력해주세요.");
    return;
  }

  try {
    console.log("🔍 diff-match-patch 비교 시작");
    console.log("원문:", originalText.value);
    console.log("비교문:", compareText.value);

    // TextComparator 버전 확인
    console.log("🔍 TextComparator 확인:");
    console.log("- TextComparator:", textComparator);

    // TextComparator는 이미 인스턴스화됨

    // TextComparator를 사용하여 텍스트 비교
    const result = textComparator.compareTexts(
      originalText.value,
      compareText.value
    );

    // 결과 저장
    diffResult.value = result;

    // structuredDiffs 기반으로 HTML 생성
    console.log("🔍 structuredDiffs 기반 HTML 생성:");
    console.log("- result.structuredDiffs:", result.structuredDiffs);
    const htmlToDisplay = structuredDiffsToHtml(result.structuredDiffs);
    diffHtml.value = htmlToDisplay;

    // 발음 오류 분석 추가
    if (analysis.value && analysis.value.srResult) {
      console.log("🔍 발음 오류 분석 시작");
      const extractedMispronunciations =
        textComparator.extractMispronunciations(
          result.structuredDiffs,
          analysis.value.srResult
        );

      mispronunciations.value = extractedMispronunciations;
      console.log("📊 추출된 발음 오류:", extractedMispronunciations);

      // 발음 오류 HTML 생성
      if (extractedMispronunciations.length > 0) {
        mispronunciationsHtml.value = textComparator.mispronunciationsToHtml(
          extractedMispronunciations,
          {
            showConfidence: true,
            showPosition: true,
            showDuration: true,
            cssClass: "mispronunciation-item",
            errorClass: "pronunciation-error",
            confidenceClass: "confidence-score",
          }
        );
        showMispronunciations.value = true;
        console.log("✅ 발음 오류 발견 - 배지 표시 활성화");
      } else {
        mispronunciationsHtml.value = "";
        showMispronunciations.value = false;
        console.log("✅ 발음 오류 없음 - 배지 표시 비활성화");
      }
    } else {
      console.log("⚠️ SR 결과가 없어서 발음 오류 분석을 건너뜁니다.");
      showMispronunciations.value = false;
    }

    console.log("✅ diff 비교 완료:", result);
    console.log("📊 통계:", result.analysis.statistics);
    console.log("🔍 디버그 정보:");
    console.log("- 원본 텍스트:", result.originalText);
    console.log("- 비교 텍스트:", result.comparedText);
    console.log("- 처리된 원본:", result.processedOriginal);
    console.log("- 처리된 비교:", result.processedCompared);
    console.log("- 삽입된 텍스트:", result.analysis.changes.insertionText);
    console.log("- 삭제된 텍스트:", result.analysis.changes.deletionText);
    console.log("- visualDiffs 존재 여부:", !!result.visualDiffs);
    if (result.visualDiffs) {
      console.log("- 시각적 diffs:", result.visualDiffs);
      console.log(
        "- 시각적 HTML:",
        textComparator.diffsToHtml(result.visualDiffs)
      );
    } else {
      console.log("- visualDiffs가 없습니다!");
    }
    console.log("- 분석용 diffs:", result.diffs);
    console.log("- 분석용 HTML:", textComparator.diffsToHtml(result.diffs));
  } catch (error) {
    console.error("❌ diff 비교 중 오류 발생:", error);
    alert("텍스트 비교 중 오류가 발생했습니다: " + error.message);
  }
}

